export enum SyncVariable {
    OfflineDB = 'offline_db'
}

export interface LabOrderOffline {
    conceptName: string;
    specimenConceptName: string;
    accessionNumber: string;
    reason: string;
    created: boolean;
}

export interface ObservationOffline {
    id: string|number;
    date?: string;
    userName?: string;
    encounterName: string;
    patientID?: number;
    encounterTypeID: number;
    observations: Array<any>;
    labOrders: LabOrderOffline[];
    uploadStatus?: "Success" | "NetworkError" | "Pending" | "Cancel" | 'Exception';
    meta?: Record<string, any>;
    created?: boolean;
}

export interface SyncData {
    id: number|string;
    label: string;
    programID: number;
    programName: string;
    status?: "Error" | "Success" | "Pending" | "Synchronizing";
    statusMessage?: string;
    date?: string;
    userName?: string;
    userID: number;
    offlineID?: number;
    personID?: number;
    personParams?: {
        npid?: string;
        person: Record<string, any>;
        attributes: Array<any>;
        created?: boolean;
    },
    observationParams?: ObservationOffline[],
    orderParams?: any;
    complete?: boolean;
}