import { TaskInterface } from "@/apps/interfaces/TaskInterface";
import { FolderInterface } from "@/apps/interfaces/AppInterface"
import { ObservationOffline } from "@/interfaces/Sync";
import HisDate from "@/utils/Date"
import { Option } from "@/components/Forms/FieldInterface";

enum WorkflowIndex {
    PREGNANCY_STATUS= 1,
    CIRCUMCISION=2,
    TESTING=3,
    REGISTRATION=4,
    RECENCY=5,
    DBS_ORDER=6,
    PARTNER_RECEPTION=7,
    APPOINTMENT=8,
    HTS_CONTACT=9,
    ITEMS_GIVEN=10,
    ART_INITIATION=11,
    REFERRAL=12
}

export const PRIMARY_ACTIVITIES: TaskInterface[] = [
    {
        id: 'pregnancy status',
        name: 'Pregnancy status',
        icon: 'current_preg.png',
        workflowID: 'PREGNANCY STATUS',
        patientUrl: `/hts/status/pregnancy`,
        offlineWorkflowIndex: WorkflowIndex.PREGNANCY_STATUS,
        offlineWorkflowCondition(params) {
            const person = params?.personParams?.person
            return person?.gender === 'F' && HisDate.getAgeInYears(person.birthdate) > 10
        },
        condition: ({ gender }: any) => gender === 'F'
    },
    {
        id: 'circumcision status',
        name: 'Circumcision status',
        icon: 'medical_male_circumcision.png',
        encounterTypeName: 'Circumcision',
        workflowID: 'CIRCUMCISION',
        patientUrl: `/hts/status/circumcision`,
        offlineWorkflowIndex: WorkflowIndex.CIRCUMCISION,
        condition: ({ gender }: any) => gender === 'M',
        offlineWorkflowCondition(params) {
            return params?.personParams?.person?.gender === 'M'
        }
    },
    {
        id: 'testing',
        name: 'Testing',
        icon: 'lab.png',
        encounterTypeName: 'Testing',
        workflowID: 'Testing',
        offlineWorkflowIndex: WorkflowIndex.TESTING,
        taskCompletionChecklist: [
            'HTS Access Type',
            'Previous HIV Test Results'
        ],
        patientUrl: `/hts/testing`,
    },
    {
        id: 'hts_registration',
        name: 'Client Registration',
        icon: 'print.png',
        encounterTypeName: 'REGISTRATION',
        workflowID: 'REGISTRATION',
        offlineWorkflowIndex: WorkflowIndex.REGISTRATION,
        patientUrl: `/hts/complete_professional_registration`,
        offlineWorkflowCondition(params) {
            const person = params?.personParams?.person
            const hasTestTwoResult = params.observationParams.some((obs: ObservationOffline) => {
                return `${obs?.encounterName}`.toLowerCase() === 'testing' &&
                    (obs?.meta?.test_results||[]).some((option: Option) => `${option.value}`.length &&
                            !option.disabled && 
                            option.other.id === 'test_2')
            })
            const nameIsUnknown = `${person.given_name} ${person.family_name}`.trim().toLowerCase() === 'unknown unknown'
            return hasTestTwoResult && nameIsUnknown
        }
    },
    {
        id: 'hts referral appointment',
        name: 'Appointment',
        icon: 'summary.png',
        workflowID: 'APPOINTMENT',
        patientUrl: `/hts/appointment`,
        offlineWorkflowIndex: WorkflowIndex.APPOINTMENT,
        offlineWorkflowCondition(params) {
            return params.observationParams.some((obs: ObservationOffline) => {
                if (!obs.meta) {
                    return false
                }
                // NOT Previous test inconclusive and current test inconclusive
                // Not positive at health facility
                return obs.encounterName === 'testing' &&
                    !(/inconclusive/i.test(obs?.meta?.last_hiv_result?.label) &&
                       /inconclusive/i.test(obs?.meta?.hiv_result?.label)) &&
                    !(/health facility/i.test(obs?.meta?.accesspoint_type?.value) &&
                        obs?.meta?.hiv_result?.label === 'Positive')
            })
        },
    },
    {
        id: 'hts art initiation',
        name: 'ART (Re-)Initiation',
        icon: 'referral.png',
        workflowID: 'ART Enrollment',
        encounterTypeName: "ART Enrollment",
        patientUrl: `/hts/art/initiation`,
        offlineWorkflowIndex: WorkflowIndex.ART_INITIATION,
        offlineWorkflowCondition: (params: any) => {
            const testing = params.observationParams.find((obs: ObservationOffline) => obs.encounterName === 'testing')
            if (!testing) return false
            const isPositive = testing.meta?.hiv_result?.label === 'Positive'
            if (isPositive &&
                testing.meta?.time_since_last_drug_taken?.other?.daysAgoInt <= 7 &&
                testing.meta?.drug_taken?.label === 'ART') {
                return false
            }
            return isPositive
        }
    },
    {
        id: 'hts referral',
        name: 'Referral',
        icon: 'referral.png',
        workflowID: 'REFERRAL',
        patientUrl: `/hts/referral`,
        offlineWorkflowIndex: WorkflowIndex.REFERRAL,
    },
    {
        id: 'hts contact',
        name: 'Contact tracing',
        icon: 'patient-type.png',
        encounterTypeName: "HTS contact",
        workflowID: 'HTS Contact',
        patientUrl: `/hts/contact`,
        offlineWorkflowIndex: WorkflowIndex.HTS_CONTACT,
        offlineWorkflowCondition: (params: any) => {
            return params.observationParams.some((obs: ObservationOffline) => {
                return obs.encounterName === 'testing' &&
                    /health facility/i.test(obs.meta?.accesspoint_type?.value) &&
                    obs.meta?.hiv_result?.label === 'Positive'
            })
        }
    },
    {
        id: 'hts partner reception',
        name: 'Partner reception',
        icon: 'edit-user.png',
        workflowID: 'Partner Reception',
        patientUrl: `/hts/partner`,
        offlineWorkflowIndex: WorkflowIndex.PARTNER_RECEPTION,
        offlineWorkflowCondition: (params: any) => {
            if (HisDate.getAgeInYears(params.personParams.person.birthdate) <= 10) {
                return false
            }
            return params.observationParams.some((obs: ObservationOffline) => {
                return obs.encounterName === 'testing' && !/community/i.test(
                    `${obs.meta?.accesspoint_type?.value}`
                    )
            })
        }
    },
    {
        id: 'hts art referral outcome',
        name: 'ART Referral Outcome',
        icon: 'aids.png',
        patientUrl: `/hts/referral/outcome`,
    },
    {
        id: 'hts items given',
        name: 'Items Given',
        icon: 'first-aid-kit.png',
        patientUrl: '/hts/items',
        offlineWorkflowIndex: WorkflowIndex.ITEMS_GIVEN,
    },
    {
        id: 'hts recency test',
        name: 'Recency',
        icon: 'usage.png',
        patientUrl: '/hts/recency',
        offlineWorkflowIndex: WorkflowIndex.RECENCY,
        offlineWorkflowCondition(params) {
            if (HisDate.getAgeInYears(params?.personParams?.person?.birthdate) <= 0)
                return false
            return params.observationParams.some((obs: ObservationOffline) => {
                if (!obs.meta) return false
                return obs.encounterName === 'testing' &&
                    obs.meta?.accesspoint_type?.label != 'Community' &&
                    (obs.meta?.test_results||[]).some((t: any) => t.other.id === 'test_2')
            })
        }
    },
    {
        id: 'hts dbs result',
        name: 'DBS Result',
        icon: 'lab.png',
        patientUrl: '/hts/dbs'
    },
    {
        id: 'dbs order',
        name: 'DBS Order',
        icon: 'lab.png',
        patientUrl: '/hts/dbs/order',
        offlineWorkflowIndex: WorkflowIndex.DBS_ORDER,
        offlineWorkflowCondition: (params: any) => {
            return params.observationParams.some((obs: ObservationOffline) => {
                const age = HisDate.getAgeInYears(params?.personParams?.person?.birthdate)
                return params.observationParams.some((t: ObservationOffline) => 
                    // patient less than 12 months with HIV positive result
                    (age <= 0 && t.encounterName === 'testing' &&
                        t?.meta?.hiv_result.label === 'Positive' &&
                        /Health facility/i.test(`${t?.meta?.accesspoint_type?.value}`
                    )) ||
                    (obs.encounterName === 'hts recency test' &&
                        obs?.meta?.recency_test?.label === 'Recent' &&
                        age > 0 && (t.encounterName === 'testing' &&
                            /inconclusive/i.test(t?.meta?.patient_category?.label)))
                )
            })
        }
    }
]

