import Apps from "@/apps/app_lib"
import { Service } from "@/services/service";

export const isOnline = ref<boolean>(true)

export const isOfflineMode = ref<boolean>(`${sessionStorage.getItem("offline_mode")}` === 'true' ? true : false)

const obsDialogLaunched = ref<boolean>(false)

const syncDialogLaunched = ref<boolean>(false)

const synchronizationStarted = ref<boolean>(false)

const offlineData = ref<SyncData[]>([])

EventBus.on(ApiBusEvents.ON_API_CRASH, () => isOnline.value = false)

EventBus.on(ApiBusEvents.AFTER_API_REQUEST, () => isOnline.value = true)


// Searches offline records by offline id
function findByID(id: string|number) {
    return offlineData.value.find((d: any) => `${d.id}` === `${id}` || `${d.personID}` === `${id}`)
}

/**
 * Builds default offline object and writes it to offline storage. 
 * @param override 
 * @returns 
 */
function saveOffline(override: any) {
    const app: any = Service.getActiveApp()
    const defaults = {
        id: randomUUID(),
        label: "Offline",
        programID: app.programID,
        programName: app.applicationName,
        status: "Pending",
        date: dayjs().format('YYYY-MM-DD hh:mm:ss'),
        userID: PatientRegistrationService.getUserID()||-1,
        userName: PatientRegistrationService.getUserName(),
    }
    const data = { ...defaults, ...override }
    // Where possible, patch an existing offline record
    let offlineRecordWithIDExists = false
    offlineData.value.forEach((offline, index) => {
        if (`${offline.id}` === `${data.id}`) {
            offlineRecordWithIDExists = true
            offlineData.value[index] = {...offline, ...data}
        }
    })
    if (!offlineRecordWithIDExists) offlineData.value.push(data)
    return defaults.id
}

/**
 * Caches Patientservice object which is preset with person information.
 * @param patient 
 * @returns 
 */
function cachePatient(patient: Patientservice) {
    const id = patient.getID()
    const newParams: any = {
        npid: patient.getNationalID(),
        created: true,
        person: {
            age: patient.getAge(),
            birthdate: patient.getBirthdate(),
            gender: patient.getGender(),
            given_name: patient.getGivenName(),
            family_name: patient.getFamilyName(),
            phone: patient.getPhoneNumber(),
            current_village: patient.getCurrentVillage(),
            current_district: patient.getCurrentDistrict(),
            current_traditional_authority: patient.getCurrentTA()
        }
    }
    // patch existing record
    const data: any = findByID(id)
    if (data) {
        data.personParams = {...(data.personParams||{}), ...newParams}
        return data
    }
    return saveOffline({
        id,
        label: "Returning client",
        personID: id,
        personParams: newParams
    })
}
/**
 * Base method for caching person information offline
 * @param label 
 * @param person 
 * @param attributes 
 * @param npid 
 * @returns 
 */
function savePersonOffline(label: string, person: Record<string, any>, attributes: Array<any>, npid=undefined as undefined|string) {
    return saveOffline({
        label,
        personParams: {
            npid,
            person,
            attributes
        }
    })
}

/**
 * Updates offline observations by writing them to existing users
 * @param params 
 * @returns 
 */
function saveObsOffline(params: ObservationOffline, created=false): string|number {
    const person = findByID(params?.id)
    if (person) {
        person.complete = created
        person.observationParams = [
            ...(person.observationParams||[]), {
                ...params,
                id: randomUUID(),
                date: dayjs().format('YYYY-MM-DD hh:mm:ss'),
                userName: PatientRegistrationService.getUserName(),
            }
        ]
    }
    return params.id
}

function hasPendingbacklog() {
    return offlineData.value.some((data: any) => !data.complete)
}

async function launchSynchronizationDialog() {
    if (syncDialogLaunched.value) return
    syncDialogLaunched.value = true;
    (await modalController.create({
        component: SyncComponent,
        backdropDismiss: false,
        cssClass: "large-modal",
        componentProps: {
            onClose: () => {
                syncDialogLaunched.value = false
                modalController.getTop().then((m: any) => m && modalController.dismiss())
            }
        }
    })).present()
}

async function launchObservationDialog(id: string|number, params={} as any) {
    if (obsDialogLaunched.value) return
    obsDialogLaunched.value = true;
    (await modalController.create({
        component: SyncObsData,
        backdropDismiss: false,
        cssClass: "large-modal",
        componentProps: {
            id,
            programID: PatientRegistrationService.getProgramID(),
            onDelete: () => {
                if (typeof params.onDeleteEncounter === 'function')
                    params.onDeleteEncounter()
            },
            onClose: () => {
                obsDialogLaunched.value = false
                modalController.getTop().then((m: any) => m && modalController.dismiss())
            }
        }
    })).present()
}

async function synchronizeRecord(offlineID: string|number): Promise<SyncData|undefined> {
    const pipeline: any = {
        "Find npid" : {
            isComplete: (params: SyncData) => params.personID,
            async handler(params: SyncData) {
                if (params?.personParams?.npid) {
                    try {
                        const results = await Patientservice.findByNpid(params?.personParams?.npid)
                        if (Array.isArray(results) && results.length <= 1)
                            params.personID = results[0].patient_id
                    } catch (e) {
                        params.personID = undefined
                        console.error(`${e}`)
                    }
                }
            }
        },
        "Register new": {
            isComplete: (params: SyncData) => params.personParams?.created,
            async handler(params: SyncData) {
                if (!params.personID && !params.personParams?.created && params?.personParams?.person) {
                    const person = new PatientRegistrationService()
                    await person.registerBasicPatient(params?.personParams?.person)
                    if ((offline?.personParams?.attributes||[]).length) {
                        await person.createPersonAttributes(
                            offline?.personParams?.attributes||[]
                        )
                    }
                    params.personID = person.getPersonID()
                    params.personParams!.created = true
                } else if (params.personID) {
                    const update = new PatientRegistrationService()
                    update.setPersonID(params.personID)
                    await update.updatePerson(params?.personParams?.person)
                }
            }
        },
        "Create Encounters": {
            isComplete: (params: SyncData) => (params.observationParams||[]).every((obs) => obs.created),
            async handler(params: SyncData) {
                for(const obs of (params.observationParams||[])) {
                    if (obs.created) continue
                    const enc = new AppEncounterService(params?.personID as number, params.userID)
                    enc.programID = params.programID
                    enc.encounterTypeID = obs.encounterTypeID
                    await enc.createEncounter()               
                    obs.created = (await enc.saveObservationList(obs.observations)) ? true : false
                }
            }
        },
        "Create lab orders": {
            isComplete: (params: SyncData) => {
                return (params.observationParams||[])
                    .every((obs) => (obs?.labOrders||[])
                        .every(order => order.created))
            },
            async handler(params: SyncData) {
                const data = (params.observationParams||[]).map(async (obs) => {
                    if (!Array.isArray(obs.labOrders)) return obs
                    const service = new LabOrderService(params.personID as number, params.userID)
                    const encounter = await service.createEncounter()
                    if (!encounter) throw new Error("Unable to create order")
                    const orders = obs?.labOrders.map((order) => {
                        return {
                            ...order,
                            concept_id: typeof order.conceptName === 'string' 
                                ? ConceptService.getCachedConceptID(order.conceptName) 
                                : order.conceptName,
                            specimenConcept: typeof order.specimenConceptName === 'string' 
                                ? ConceptService.getCachedConceptID(order.specimenConceptName) 
                                : order.specimenConceptName
                        }
                    })
                    const orderResponse = await OrderService.saveOrdersArray(
                        encounter?.encounter_id, 
                        OrderService.buildLabOrders(encounter, orders)
                    )
                    const orderOk = orderResponse ? true : false
                    obs.labOrders = orders.map((orders) => {
                        orders.created = orderOk
                        return orders
                    })
                    return obs
                })
                params.observationParams = await Promise.all(data)
            }
        },
        "Finish": {
            handler(params: SyncData) {
                params.status = "Success"
                params.statusMessage = "Synchronization complete"
            }
        }
    }

    const offline = findByID(offlineID)

    if (!offline) return

    offline.status = 'Synchronizing'
    offline.statusMessage = 'Initiating'

    for (const key in pipeline) {
        const process: any = pipeline[key]
        if (typeof process.isComplete === 'function' && !process.isComplete(offline)) {
            try {
                offline!.statusMessage = key
                await pipeline[key].handler(offline)
            } catch (e) {
                offline.status = "Error"
                offline.statusMessage = `Error while processing "${key}"`
                console.error("Synchronization pipeline error : '" + key + "'", e)
                return offline
            }
        }
    }
    return offline
}

/**
 * Uploads offline created persons, observations and lab orders
 * @returns 
 */
async function synchronize(): Promise<void> {
    if (synchronizationStarted.value||!offlineData.value.length) 
        return
    toastSuccess("Synchronization started")
    for (const offline of offlineData.value) {
        if (offline.complete) {
            continue
        }
        synchronizationStarted.value = true
        try {
            await synchronizeRecord(offline.id)
        } catch (e) {
            continue
        }
    }
    offlineData.value = offlineData.value.filter((d: SyncData) => !d.complete && d.status === 'Error')
    synchronizationStarted.value = false
    toastSuccess("Synchronization Complete")
}

/**
 * Provides an offline workflow engine for determining next activity name based on PatientID
 * And activities that haven't been done for the patient.
 * today.
 * @param patientID 
 * @returns 
 */
function nextPatientWorkflowActivity(patientID: string|number): TaskInterface|undefined {
    // Get offline data by patientID
    const offlineData = findByID(patientID)
    if (!offlineData) return
    const observationParams = (offlineData.observationParams||[])
    const activities = (Apps.getActiveApp()?.primaryPatientActivites||[])
        .filter((activity) => typeof activity.offlineWorkflowIndex === 'number')
        .sort((a: any, b: any) => a.offlineWorkflowIndex - b.offlineWorkflowIndex)
    for (const activity of activities) {
        const doneToday = observationParams.some((obs: ObservationOffline) => {
            if (obs.encounterName === activity.id)
                return dayjs(obs.date).format('YYYY-MM-DD') === Patientservice.getSessionDate();
            return false
        })
        if (doneToday) continue
        // For activities with workflow conditions, run validations to determine 
        // whether to carryout the activity or not
        if (typeof activity.offlineWorkflowCondition === 'function') {
            const ok = activity.offlineWorkflowCondition({
                ...offlineData, observationParams
            })
            if (!ok) continue
        }
        return activity
    }
}

/**
 * Initiate in memory storage by retrieving data from offline storage
 */
if (!offlineData.value || !offlineData.value.length) {
    offlineData.value = JSON.parse(localStorage.getItem(SyncVariable.OfflineDB)||'[]')
}

/**
 * Updates offline storage everytime memory stored values change
 */
watch(offlineData, (data: any) => {
    localStorage.setItem(SyncVariable.OfflineDB, JSON.stringify(data))
}, { deep: true })

/**
 * Tracks offline mode in localstorage
 */
watch(isOfflineMode, (offline: any) => sessionStorage.setItem("offline_mode", `${offline}`))

export function Offline() : any {
    return {
        isOnline,
        isOfflineMode,
        offlineData,
        saveOffline,
        findByID,
        synchronize,
        saveObsOffline,
        cachePatient,
        hasPendingbacklog,
        savePersonOffline,
        synchronizeRecord,
        synchronizationStarted,
        launchObservationDialog,
        launchSynchronizationDialog,
        nextPatientWorkflowActivity
    }
}
