import { Service } from "@/services/service"
import HisDate from "@/utils/Date"

export class HtsReportService extends Service {
    startDate: string
    endDate: string
    date: string
    name: string
    programID: number
    constructor(
        name: string,
        date=Service.getSessionDate(), 
        programID=Service.getProgramID()
        ) {
        super()
        this.startDate = ''
        this.endDate = ''
        this.date = date
        this.name = name
        this.programID = programID
    }

    requestReport(params: any={}) {
        // This endpoint doesnt exist yet
        return Service.getJson('hts_reports', {
            'start_date': this.startDate,
            'end_date': this.endDate,
            'name': this.name,
            ...params
        })
    }

    periodStr() {
        const d = (date: string) => HisDate.toStandardHisDisplayFormat(date)
        return `${d(this.startDate)} - ${d(this.endDate)}`
    }
}
