import { Field } from "@/components/Forms/FieldInterface";
import { modalController } from "@ionic/vue";
import TouchField from "@/components/Forms/SIngleTouchField.vue"
import TouchScreenForm from "@/components/Forms/TouchScreenForm.vue";
import { MultiStepFormParams } from "@/interfaces/HisFormTypes";
import RadioSelection from '@/components/DataViews/actionsheet/RadioActionSheet.vue'
import Keypad from "@/components/Keyboard/HisKeypad.vue"

export async function selectionList(list: string[], onValue: (selection: string) => void, params={} as any) {
    const modal = await modalController.create({
        component: RadioSelection,
        backdropDismiss: false,
        cssClass: `${params?.size || 'list-modal'} custom-modal-backdrop`,
        componentProps: {
            list,
            preset: params?.preset || '',
            actionButtons: [
                { name: 'Cancel', slot:'start'}
            ],
            closeOnSelect: true,
            title: params?.title || 'Select'
        }
    })
    await modal.present()
    const { data } = await modal.onDidDismiss()
    onValue(data.selection as string)
}

export async function numericKeypad(onValue: (num: any, toggle?: boolean) => void, params={} as any) {
    const useStrictNumbers = typeof params?.strictNumbers === 'boolean' ? params?.strictNumbers : true
    const modal = await modalController.create({
        component: Keypad,
        backdropDismiss: false,
        cssClass: 'keypad-modal custom-modal-backdrop',
        componentProps: {
            title: params?.title || '',
            preset: params?.preset || '',
            toggleButton: params?.toggleButton,
            strictNumbers: useStrictNumbers,
            customKeyboard: params?.customKeyboard,
            noFloat: typeof params?.noFloat === 'boolean'  ? params.noFloat : false,
            onKeyPress(val: string, toggle: boolean) {
                onValue(useStrictNumbers ? parseInt(val) : val, toggle)
            }
        }
    })
    modal.present()
}

export async function MultiStepPopupForm(fields: Field[], params={} as MultiStepFormParams) {
    const modal = await modalController.create({
        component: TouchScreenForm,
        backdropDismiss: false,
        cssClass: "full-modal",
        componentProps: {
            fields: fields,
            formLabel: params.formLabel,
            disableAutoModalDismiss: true,
            cancelAction: () => {
                modalController.dismiss()
                typeof params.onCancel === 'function' && params.onCancel()
            },
            onFinish: params.onFinish,
            skipSummary: typeof params.skipSummary === 'boolean' ? params.skipSummary : false
        }
    })
    modal.present()
    return modal
}

export default async function PopupKeyboard(field: Field, onFinish: any) {
    const modal = await modalController.create({
        component: TouchField,
        backdropDismiss: false,
        cssClass: "full-modal",
        componentProps: {
            dismissType: 'modal',
            currentField: field,
            onFinish
        }
    })
    modal.present()
    return modal
}