import Validation from "@/components/Forms/validations/StandardValidations"
import { FieldType } from '@/components/Forms/BaseFormElements'
import { Field, Option } from '@/components/Forms/FieldInterface'
import { isEmpty, isPlainObject } from 'lodash'
import HisDate from "@/utils/Date"
import { Service } from "@/services/service"
import { loadingController, modalController } from "@ionic/vue"
import DrilldownTable from "@/apps/ART/views/reports/BasicReportTemplate.vue"
import table from "@/components/DataViews/tables/ReportDataTable"
import { Patientservice } from "@/services/patient_service"
import { useRouter } from "vue-router"
import { computed, onUnmounted, reactive, watch } from "vue"
import { generateDateFields } from "@/utils/HisFormHelpers/MultiFieldDateHelper"
import { ReportConfig, HtsReportColumns, WindowPrintParamInterface, DrillDownParams } from "../types/ReportTypes"
import { MultiStepPopupForm } from "@/utils/PopupKeyboard"
import { toastWarning } from "@/utils/Alerts"
import { toDate } from "@/utils/Strs"
import dayjs from "dayjs"
import EventBus, { EventChannels } from "@/utils/EventBus"

enum DatePeriodInputType {
    MULTI_FIELD_DATE = 'Multipage date fields',
    CALENDAR_FIELD = 'Calendar',
    SINGLE_DATE_INPUT_FIELD = 'Single page date'
}

const reportStore = reactive({} as Record<string, any>)

export enum HtsReportPreference {
    CAN_CACHE = 'hts_report_can_cache',
    PERIOD_INPUT_TYPE = 'hts_report_date_period_type'
}

export const ReportPreferences = reactive({
    canCacheReport: localStorage.getItem(HtsReportPreference.CAN_CACHE) || false,
    datePeriodInputType: localStorage.getItem(HtsReportPreference.PERIOD_INPUT_TYPE) || DatePeriodInputType.MULTI_FIELD_DATE
} as Record<string, any>)

export function HtsReportComposable(reportTitle='Report') {
    if (!(reportTitle in reportStore)) {
        reportStore[reportTitle] = {
            title: '' as string,
            dateGenerated: '' as string,
            other: {} as Record<string, any>,
            appVersion: Service.getCoreVersion() as string,
            apiVersion: Service.getApiVersion() as string,
            rows: [] as Array<any>,
            columns: [] as any,
            intermediateColumns: [] as HtsReportColumns[],
            fields: [] as Field[],
            period: '' as string,
            drill: [] as any,
            data: [] as Array<any> | {},
            prePopulateData: [],
            prefix: '',
            indicatorData: {} as Record<string, any>,
            indicators: {} as Record<string, any>,
            initialSort: null as null | Function,
            formData: {} as Record<string, any>,
            computedFormData: {} as Record<string, any>,
            generationFunc: null as Function | null
        }
    }
    const indicators = computed(()=> reportStore[reportTitle]?.indicators)
    const rows = computed(() => reportStore[reportTitle]?.rows || [])
    const period = computed(() => reportStore[reportTitle]?.period || '')
    const fields = computed(() => reportStore[reportTitle]?.fields || [])
    const columns = computed(() => reportStore[reportTitle]?.columns || [])
    const title = computed(() => reportStore[reportTitle]?.title || reportTitle)
    const router = useRouter()
    const dateGenerated = computed(() => reportStore[reportTitle]?.dateGenerated || '-')

    function clearReports() {
        for (const prop in reportStore) { 
            reportStore[prop] = {
                title: reportTitle as string,
                dateGenerated: '' as string,
                other: {} as Record<string, any>,
                appVersion: Service.getCoreVersion() as string,
                apiVersion: Service.getApiVersion() as string,
                rows: [] as Array<any>,
                columns: [] as any,
                intermediateColumns: [] as HtsReportColumns[],
                fields: [] as Field[],
                period: '' as string,
                drill: [] as any,
                data: [] as any,
                initialSort: null as null | Function,
                formData: {} as Record<string, any>,
                computedFormData: {} as Record<string, any>,
                generationFunc: null as Function | null
            }
        }
    }

    function reportDescription() {
        return `${reportStore[reportTitle].prefix || ''}-${reportTitle}-${Service.getLocationName()}-${period.value}`
    }

    function gotoPatientDashboard(patientId: number) {
        router.push(`/patient/dashboard/${patientId}`)
    }

    function appendReportStamp(rows=[] as Array<any>) {
        return [
        ...rows,
            [],
            [],
            [`Date Created: ${reportStore[reportTitle].dateGenerated}`],
            [`Quarter: ${reportStore[reportTitle].period}`],
            [`HIS-Core Version: ${reportStore[reportTitle].appVersion}`],
            [`API Version: ${reportStore[reportTitle].apiVersion}`],
            [`Site UUID: ${Service.getSiteUUID()}`]
        ]
    }

    /**
     * Trigger browser's print action
     * @param params 
     */
    function showPrintWindow(params: WindowPrintParamInterface) {
        const printW = open('', '', 'width:1024px, height:768px')
        const content = document.getElementById(params.containerID)
        if (content && printW) {
            printW.document.write(`
                <html>
                <head>
                    <title>${content.title}</title>
                    <link rel="stylesheet" media="print"  href="${params.cssFile}"/>
                </head>
                <body>
                    ${content.innerHTML}
                </body>
                </html>
            `)
            setTimeout(() => { printW.print(); printW.close() }, 3500)
        }
    }

    async function drillModal(params={} as DrillDownParams) {
        (await modalController.create({
            component: DrilldownTable,
            cssClass: 'large-modal',
            componentProps: {
                title: params.title,
                columns: params.columns,
                rows: params.data,
                footerColor: 'light',
                rowParser: (rows: any) => params.generator(rows),
                paginated: true,
                showFilters: true,
                rowsPerPage: 20,
                showReportStamp: false,
                onFinish: () => modalController.dismiss()
            }
        })).present()
    }

    function defaultDrillDown(title: string, patientIds: number[]) {
        drillModal({
            title,
            columns: [
                [
                    table.thTxt('National ID'),
                    table.thTxt('First name'),
                    table.thTxt('Last name'),
                    table.thTxt('Birthdate'),
                    table.thTxt('Action')
                ]
            ],
            data: patientIds,
            generator: async (rows: any) => {
                const row = []
                for(const id of rows) {
                    if (reportStore[reportTitle].drill[id]) {
                        row.push(reportStore[reportTitle].drill[id])
                        continue
                    }
                    try {
                        const patient = new Patientservice((await Patientservice.findByID(id))) 
                        reportStore[reportTitle].drill[id] = [
                            table.td(patient.getNationalID()),
                            table.td(patient.getGivenName()),
                            table.td(patient.getFamilyName()),
                            table.tdDate(`${patient.getBirthdate()}`),
                            table.tdBtn('Show', () => {
                                router.push({ path: `/patient/dashboard/${id}`})
                                modalController.dismiss({})
                            })
                        ]
                        row.push(reportStore[reportTitle].drill[id])
                    } catch (e) {
                        console.error(e)
                    }
                }
                return row
            }
        })
    }

    /**
     * Show drilldown table based on provided patient identifiers
     * @param title 
     * @param patientIds 
     * @returns 
     */
    function drill(title: string, patientIds: number[]) {
        if (patientIds.length) {
            return table.tdLink(patientIds.length, () => defaultDrillDown(title, patientIds))
        }
        return table.td(0)
    }

    function drillCustom(values: Array<any>, drillParams: DrillDownParams, staticValue=undefined) {
        if (values.length) {
            return table.tdLink(staticValue || values.length, () => drillModal(drillParams))
        }
        return table.td(0)
    }

    async function updateReportData() {
        const { generationFunc, data, formData, computedFormData, prePopulateData } = reportStore[reportTitle]
        if (typeof generationFunc != 'function') {
            return;
        } 
        (await loadingController.create({
            message: 'Please wait..', 
            backdropDismiss: false
        })).present();
        try {
            reportStore[reportTitle].drill = []
            reportStore[reportTitle].rows = []
            reportStore[reportTitle].data = []
            const res = await generationFunc(data, formData, computedFormData)
            reportStore[reportTitle].dateGenerated = dayjs().format('DD/MM/YYYY hh:mm:ss')        
            if (!isEmpty(res)) {
                reportStore[reportTitle].data = res
            }
            if (!isEmpty(reportStore[reportTitle].data)) {
                // Run sort function to sort data initially
                if (typeof reportStore[reportTitle].initialSort === 'function') {
                    reportStore[reportTitle].data = reportStore[reportTitle].initialSort(reportStore[reportTitle].data)
                }
            }
            loadingController.dismiss()
        } catch (e) {
            if (!isEmpty(prePopulateData)) {
                reportStore[reportTitle].data = prePopulateData
            }
            reportStore[reportTitle].dateGenerated = '_ERROR_'
            console.error(`${e}`)
            toastWarning(`${e}`)
            loadingController.dismiss()
        }
    }

    function setReportPreferences() {
        MultiStepPopupForm([
            {
                id: 'configuration_options',
                helpText: 'Select setting to change',
                type: FieldType.TT_MULTIPLE_SELECT,
                isRequired: () => true,
                options: () => {
                    return [
                        { label: 'Date selection', value: 'date_selection' },
                        { label: 'Report caching', value: 'caching' }
                    ]
                }
            },
            {
                id: 'date_field_type',
                helpText: 'Report period input type',
                type: FieldType.TT_SELECT,
                isRequired: () => true,
                defaultValue: () => ReportPreferences.datePeriodInputType,
                condition: (f: any) => f.configuration_options.some((o: Option) => /date_selection/i.test(`${o.value}`)), 
                options: () => {
                    return [
                        { label: 'Calendar', value: DatePeriodInputType.CALENDAR_FIELD },
                        { label: 'Single page date', value: DatePeriodInputType.SINGLE_DATE_INPUT_FIELD},
                        { label: 'Multipage date fields', value: DatePeriodInputType.MULTI_FIELD_DATE },
                    ]
                }
            },
            {
                id: 'report_memory',
                helpText: 'Persist reports in Memory?',
                type: FieldType.TT_SELECT,
                isRequired: () => true,
                defaultValue: () => ReportPreferences.canCacheReport,
                condition: (f: any) => f.configuration_options.some((o: Option) => /caching/i.test(`${o.value}`)), 
                options: () => {
                    return [
                        { label: 'Yes', value: 'Yes' },
                        { label: 'No', value: 'No' }
                    ]
                }
            }
        ],
        {
            skipSummary: true,
            onFinish: (f: any) => {
                if (f.report_memory?.value) {
                    ReportPreferences.canCacheReport = f.report_memory.value
                    if (ReportPreferences.canCacheReport === 'No') clearReports()
                }
                if (f.date_field_type?.value) {
                    ReportPreferences.datePeriodInputType = f.date_field_type.value
                }
                modalController.dismiss()
            }
        })
    }

    function setReportPeriod(params: ReportConfig) {
        MultiStepPopupForm([
            ...generateDateFields({
                id: 'start_date',
                helpText: 'Start',
                required: true,
                minDate: () => params?.minDate || '',
                maxDate: () => params?.maxDate || Service.getSessionDate(),
                condition: () => (params.isPeriodSelection && ReportPreferences.datePeriodInputType === DatePeriodInputType.MULTI_FIELD_DATE) as boolean,
                estimation: {
                    allowUnknown: false
                },
                computeValue: (date: string) => date 
            }),
            ...generateDateFields({
                id: 'end_date',
                helpText: 'End',
                required: true,
                minDate: (_: any, c: any) => c.start_date,
                maxDate: () => params?.maxDate || Service.getSessionDate(),
                condition: () => (params.isPeriodSelection && ReportPreferences.datePeriodInputType === DatePeriodInputType.MULTI_FIELD_DATE) as boolean,
                estimation: {
                    allowUnknown: false
                },
                computeValue: (date: string) => date
            }),
            {
                id: 'alt_start_date',
                proxyID: 'start_date',
                helpText: 'Start Date',
                type: FieldType.TT_FULL_DATE,
                isRequired: () => true,
                validation: (v: Option) => {
                    const maxDate = params.maxDate || Service.getSessionDate()
                    if (params.minDate && new Date(v.value) < new Date(params.minDate)) {
                        return [`${toDate(`${v.value}`)} is less than minimum date of ${toDate(params.minDate)}`]
                    }
                    if (maxDate && new Date(v.value) > new Date(maxDate)) {
                        return [`${toDate(`${v.value}`)} is greater than max date of ${toDate(maxDate)}`]
                    }
                    return null
                },
                computedValue: (v: Option) => v.value,
                condition: () => params.isPeriodSelection && ReportPreferences.datePeriodInputType === DatePeriodInputType.SINGLE_DATE_INPUT_FIELD
            },
            {
                id: 'alt_end_date',
                proxyID: 'end_date',
                helpText: 'End Date',
                type: FieldType.TT_FULL_DATE,
                isRequired: () => true,
                defaultValue: (f: any) => f.start_date.value,
                validation: (v: Option, f: any) => {
                    const maxDate = params.maxDate || Service.getSessionDate()
                    if (f.start_date.value && new Date(v.value) < new Date(f.start_date.value)) {
                        return [`${toDate(`${v.value}`)} is less than minimum date of ${toDate(f.start_date.value)}`]
                    }
                    if (maxDate && new Date(v.value) > new Date(maxDate)) {
                        return [`${toDate(`${v.value}`)} is greater than max date of ${toDate(maxDate)}`]
                    }
                    return null
                },
                computedValue: (v: Option) => v.label,
                condition: () => params.isPeriodSelection && ReportPreferences.datePeriodInputType === DatePeriodInputType.SINGLE_DATE_INPUT_FIELD
            },
            {
                id: 'calendar_start_date',
                proxyID: 'start_date',
                helpText: 'Start Date',
                type: FieldType.TT_DATE_PICKER,
                isRequired: () => true,
                computedValue: (v: Option) => v.value,
                condition: () => params.isPeriodSelection && ReportPreferences.datePeriodInputType === DatePeriodInputType.CALENDAR_FIELD,
                config: {
                    minDate: () => params.minDate || null,
                    maxDate: () => params.maxDate || Service.getSessionDate(),
                    infoItems: (date: string, f: any) => {
                        return [
                            { label: 'Start Date', value: toDate(date) || '-' },
                            { label: 'End Date', value: toDate(f.end_date?.value) || '-'}
                        ]
                    },
                    persistInfoItems: false
                }
            },
            {
                id: 'calendar_end_date',
                proxyID: 'end_date',
                helpText: 'End Date',
                type: FieldType.TT_DATE_PICKER,
                defaultValue: (f: any) => f.start_date.value,
                isRequired: () => true,
                computedValue: (v: Option) => v.value,
                condition: () => params.isPeriodSelection && ReportPreferences.datePeriodInputType === DatePeriodInputType.CALENDAR_FIELD,
                config: {
                    minDate: (f: any) => f.start_date.value,
                    maxDate: () => params.maxDate || Service.getSessionDate(),
                    infoItems: (date: string, f: any) => {
                        return [
                            { label: 'Start Date', value: toDate(f.start_date?.value) || '-' },
                            { label: 'End Date', value: toDate(date)}
                        ]
                    },
                    persistInfoItems: false
                }
            },
            {
                id: 'year',
                helpText: 'Select Year',
                type: FieldType.TT_NUMBER,
                isRequired: () => true,
                computedValue: (v: Option) => v.value,
                condition: () => params.isMonthlySelection, 
                validation: (v: Option) => {
                    const year = isPlainObject(v) ? v.value : -1
                    return Validation.validateSeries([
                        () => {
                            if (isNaN(parseInt(`${year}`))) {
                                return ['Invalid year']
                            }
                            return null
                        },
                        () => Validation.rangeOf(v, 2000, HisDate.getYear(Service.getSessionDate()))
                    ])
                }
            },
            {
                id: 'month',
                helpText: 'Select Month',
                type: FieldType.TT_SELECT,
                isRequired: () => true,
                computedValue: (v: Option) => v.value,
                condition: () => params.isMonthlySelection, 
                options: () => {
                    return [
                        {label: 'January', value: '01'},
                        {label: 'February', value: '02'},
                        {label: 'March', value: '03'},
                        {label: 'April', value: '04'},
                        {label: 'May', value: '05'},
                        {label: 'June', value: '06'},
                        {label: 'July', value: '07'},
                        {label: 'August', value: '08'},
                        {label: 'September', value: '09'},
                        {label: 'October', value: '10'},
                        {label: 'November', value: '11'},
                        {label: 'December', value: '12'}
                    ]
                }
            },
            {
                id: 'start_n_end_date_period',
                proxyID: 'report_period',
                helpText: 'report_period',
                type: FieldType.TT_HIDDEN,
                condition: (f: any, c: any) => c.start_date && c.end_date,
                computedValue: (_: any, f: any, c: any) => {
                    return `${toDate(c.start_date)}-${toDate(c.end_date)}`
                }
            }
        ], 
        {
            skipSummary: true,
            formLabel: reportTitle,
            onFinish: (f: any, c: any) => {
                modalController.dismiss()
                reportStore[reportTitle].dateGenerated = '-'
                reportStore[reportTitle].formData = f
                reportStore[reportTitle].computedFormData = c
                reportStore[reportTitle].generationFunc = params.generate
                reportStore[reportTitle].period = c.report_period || 'N/A'
                reportStore[reportTitle].initialSort = params.initialSort || null
                reportStore[reportTitle].prefix = params?.prefix || ''
                updateReportData()
            },
            onCancel: () => !reportStore[reportTitle].period ? router.push('/') : null
        })
    }

    onUnmounted(() => {
        if (ReportPreferences.canCacheReport === 'No' && reportStore[reportTitle]) {
            delete reportStore[reportTitle]
        }
    })

    watch(() => ReportPreferences.datePeriodInputType, (input) => {
        localStorage.setItem(HtsReportPreference.PERIOD_INPUT_TYPE, input)
    })

    watch(() => ReportPreferences.canCacheReport, (canCache) => {
        localStorage.setItem(HtsReportPreference.CAN_CACHE, canCache)      
    })

    EventBus.on(EventChannels.LOGOUT, () => clearReports())

    return {
        drill,
        drillCustom,
        setReportPreferences,
        updateReportData,
        defaultDrillDown,
        showPrintWindow,
        setReportPeriod,
        gotoPatientDashboard,
        reportDescription,
        appendReportStamp,
        dateGenerated,
        ReportPreferences,
        reportData: reportStore[reportTitle],
        indicators,
        rows,
        period,
        fields,
        columns,
        title
    }
}
