
import { defineComponent, computed, ref } from 'vue'
import {
    IonIcon,
    IonProgressBar,
    IonLabel,
    IonButton,
    IonPage,
    IonToolbar,
    IonHeader,
    IonTitle,
    IonContent,
    IonList,
    IonFooter,
    IonItem,
    IonBadge
} from "@ionic/vue"
import { Offline } from "@/composables/offline"
import {
    trash,
    checkmarkCircle
} from "ionicons/icons"
import { alertConfirmation } from '@/utils/Alerts'
import { SyncData } from '@/interfaces/Sync'
import { toDate } from "@/utils/Strs"

export default defineComponent({
    components: {
        IonIcon,
        IonProgressBar,
        IonBadge,
        IonLabel,
        IonButton,
        IonToolbar,
        IonList,
        IonItem,
        IonPage,
        IonHeader,
        IonTitle,
        IonContent,
        IonFooter
    },
    props: {
        id: {
            type: String,
            required: true
        },
        programID: {
            type: Number,
            required: true
        },
        onClose: {
            type: Function
        },
        onDelete: {
            type: Function
        }
    },
    setup(props) {
        const { offlineData, findByID } = Offline()
        const synchronizationStarted = ref(false)
        const listData = computed(() => {
            if (!props.id) {
                return []
            }
            const person = findByID(props.id)
            if (!person ||
                person.programID != props.programID ||
                !person.observationParams) {
                return []
            }
            return person.observationParams.map((obs: any) => ({
                ...obs,
                createdBy: person.createdBy,
                status: person.status
            }))
        })

        function close() {
            if (typeof props.onClose === 'function') {
                props.onClose()
            }
        }

        function removeEncounter(id: string) {
            alertConfirmation("Are you sure you want to delete observation?")
                .then((ok) => {
                    if (!ok) return
                    offlineData.value = offlineData.value.map((sync: SyncData) => {
                        if (`${sync.id}` === `${props.id}` && sync.observationParams) {
                            sync.observationParams = sync.observationParams
                                .filter((obs) => obs.id!= id)
                        }
                        return sync
                    })
                    if (typeof props.onDelete === 'function') {
                        props.onDelete()
                    }
                })
        }

        return {
            close,
            toDate,
            removeEncounter,
            listData,
            checkmarkCircle,
            trash,
            synchronizationStarted
        }
    }
})
