
import { defineComponent } from 'vue'
import BaseKeyboard from '@/components/Keyboard/BaseKeyboard.vue'
import { DEFAULT_KEYPAD } from '@/components/Keyboard/KbLayouts'
import handleVirtualInput from '@/components/Keyboard/KbHandler'
import { modalController } from '@ionic/core'
import {
    IonChip,
    IonItem,
    IonLabel,
    IonCheckbox
} from "@ionic/vue"
export default defineComponent({
    components: { 
        IonChip,
        BaseKeyboard, 
        IonItem,
        IonLabel,
        IonCheckbox
    },
    props: {
        title: {
            type: String
        },
        toggleButton: {
            type: String
        },
        preset: {
            type: String
        },
        onKeyPress: {
            type: Function,
            required: true,
        },
        noFloat: {
            type: Boolean
        },
        customKeyboard: {
            type: Array
        },
        strictNumbers: {
            type: Boolean,
            default: false
        }
    },
    data: () => ({
        value: '0',
        toggle: false,
        keypad: DEFAULT_KEYPAD as any[]
    }),
    watch: {
        customKeyboard: {
            handler(keypad: any[]) {
                if (Array.isArray(keypad)) this.keypad = keypad
            },
            immediate: true
        },
        preset: { 
            handler(value: string | number) {
                if (!value) return
                this.value = value.toString()
            },
            immediate: true
        },
        toggle: {
            handler(bool: boolean) {
                this.onKeyPress(parseInt(this.value), bool)
            }
        }
    },
    methods: {
        async keypress(key: any) {
            if (key.match(/done/i)) {
                await modalController.dismiss(this.value)
            } else {
                if (this.strictNumbers) {
                    if (key.includes('.') && this.value.includes('.')) return
                    
                    if (!key.includes('.') && this.value === '0') this.value = ''

                    this.value = handleVirtualInput(key, this.value)

                    if (!this.value) this.value = '0'
                    // Condition verifies number as interger and emits parsed integer
                    if (`${parseInt(this.value)}` === this.value) {
                        this.onKeyPress(parseInt(this.value), this.toggle)
                    } else if (typeof this.noFloat === 'boolean' && this.noFloat) {
                        this.onKeyPress(parseInt(this.value), this.toggle)
                    } else {
                        // helps preserve floating point values 
                        this.onKeyPress(this.value, this.toggle)
                    }
                } else {
                    this.value = handleVirtualInput(key, this.value)
                    this.onKeyPress(this.value, this.toggle)
                }
            }
        }
    }
})
