
import { defineComponent, ref, watch } from 'vue'
import {
    IonRow,
    IonGrid,
    IonCol,
    IonText
} from "@ionic/vue"
import { HtsReportService } from "@/apps/HTS/services/hts_report_service"
import { isOnline } from "@/composables/offline"

export default defineComponent({
    components: {
        IonGrid,
        IonRow,
        IonCol,
        IonText
    },
    setup() {
        const totalRegistered = ref(0)
        const totalEnrolledInArt = ref(0)
        const totalTestedReturning = ref(0)
        const totalPositive = ref(0)

        async function init() {
            const report = new HtsReportService('HTS DASHBOARD STATS')
            report.startDate = HtsReportService.getSessionDate()
            report.endDate = report.startDate
            const stats = (await report.requestReport()) || {}
            totalRegistered.value = stats.total_registered || 0
            totalEnrolledInArt.value = stats.total_enrolled_into_art || 0
            totalTestedReturning.value = stats.total_tested_returning || 0   
            totalPositive.value = stats.total_positive || 0      
        }

        watch(isOnline, (online) => {
            if (online) init()
        }, { immediate: true })
        return {
            totalPositive,
            totalRegistered,
            totalEnrolledInArt,
            totalTestedReturning
        }
    }
})
