
import {
  IonContent,
  IonButton,
  IonHeader,
  IonTitle,
  IonToolbar,
  modalController,
  IonCheckbox,
} from "@ionic/vue";
import { defineComponent, PropType } from "vue";
import { toastWarning } from "@/utils/Alerts"
import { ActivityInterface } from "@/apps/interfaces/AppInterface"
import { isEmpty } from "lodash";
import MultiColumnView from "./containers/MultiColumnView.vue";
import UserProp from '@/composables/userproperty'

export default defineComponent({
  name: "Modal",
  props: {
    activities: {
      type: Object as PropType<ActivityInterface[]>,
      required: true
    },
    programID: {
      type: Number,
    },
    property: {
      type: String, 
      default: "activities"
    },
  },
  watch: {
    activities: {
      handler(activities: Array<ActivityInterface>){
        if (activities) {
          this.appActivities = [...activities]
          this.getActivities();
        }
      },
      immediate: true
    }
  },
  methods: {
    async getActivities() {
      const  { getUserProp } = UserProp()
      const data = await getUserProp(this.property)
      if(!isEmpty(data)){
        this.appActivities = this.appActivities.map((el) => {
          if (data.search(el.value) >= 0) {
            el.selected = true;
          }
          return el;
        });
      } else {
        toastWarning("Activities not found");
      }
    },
    async postActivities() {
      const { setUserProp } = UserProp()
      const res = await setUserProp(this.property, this.selectedActivities || 'None', this.programID)
      if (!res) {
        toastWarning("Could not save activities");
      } else {
        modalController.dismiss();
      }
    },
  },
  computed: {
    selectedActivities(): string {
      return this.appActivities
        .filter((element) => element.selected == true)
        .map((el) => el.value )
        .join(",");
    },
  },
  data() {
    return {
      content: "Content",
      appActivities: [] as Array<ActivityInterface>,
    };
  },
  components: {
    IonButton,
    IonContent,
    IonHeader,
    IonTitle,
    IonToolbar,
    IonCheckbox,
    MultiColumnView
},
});
