import { Service } from "@/services/service"
import { ref } from "vue"
import { toastWarning } from "@/utils/Alerts"

const userProperties = ref<Record<string|number, any>>(
    JSON.parse(localStorage.getItem("user_properties")||'{}')|| {}
)

function getUserID() { return Service.getUserID()||-1 }

async function getUserProp(property: string) {
    try {
        return userProperties.value[getUserID()][Service.getProgramID()][property]
    } catch (e) {
        console.warn(`${e}`)
    }
    try {
        const prop = await Service.getJson('user_properties', { property: property })
        if (prop) {
            return prop?.property_value
        }
    } catch (e) {
        console.error(`${e}`)
    }
}

async function setUserProp(property: string, data: any, programID=Service.getProgramID() as number) {
    const userID = getUserID();

    if (!userProperties.value[userID]) {
        userProperties.value[userID] = { };
    }

    if (!userProperties.value[userID][programID]) {
        userProperties.value[userID] = {
            [programID] : {}
        }
    }

    if (!(property in userProperties.value[userID][programID])) {
        userProperties.value[userID][programID][property] = null;
    }

    try {
        userProperties.value[userID][programID][property] = data;
        localStorage.setItem("user_properties", JSON.stringify(userProperties.value))
    } catch (e) {
        console.log('Error occurred while setting user property:', e);
    }

    const activity = {
        property,
        property_value: data
    };
    try {
        await Service.postJson('user_properties', activity)
    } catch (e) {
        console.error(`${e}`)
        toastWarning("Unable to synchronize user properties")
    }
    return activity
}

export default function UserProperty() {
    return {
        getUserProp,
        setUserProp
    }
}