
import { defineComponent } from 'vue'
import {
    IonIcon,
    IonProgressBar,
    IonLabel,
    IonButton,
    IonPage,
    IonToolbar,
    IonHeader,
    IonTitle,
    IonContent,
    IonList,
    IonFooter,
    IonItem,
    IonBadge
} from "@ionic/vue"
import { Offline, isOfflineMode, isOnline } from "@/composables/offline"
import isEmpty from "lodash/isEmpty"
import {
    checkmarkCircle,
    trash,
    document
} from "ionicons/icons"
import { alertConfirmation } from '@/utils/Alerts'
import router from '@/router/index';
import { toDate } from "@/utils/Strs"

export default defineComponent({
    components: {
        IonIcon,
        IonProgressBar,
        IonBadge,
        IonLabel,
        IonButton,
        IonToolbar,
        IonList,
        IonItem,
        IonPage,
        IonHeader,
        IonTitle,
        IonContent,
        IonFooter
    },
    props: {
        onClose: {
            type: Function
        }
    },
    setup(props) {
        const { offlineData, synchronizationStarted, synchronize } = Offline()

        function close() {
            if (isOnline.value) {
                isOfflineMode.value = false
            }
            if (typeof props.onClose === 'function') {
                props.onClose()
            }
        }

        function gotoDashboard(id: string) {
            close()
            router.push(`/patient/dashboard/${id}`)
        }

        async function startSync() {
            if (isOnline.value) {
                isOfflineMode.value = false
            }
            await synchronize()
        }

        function removeSync(id: number|string) {
            alertConfirmation("Are you sure you want to delete backlog?")
                .then((ok) => {
                    if (ok) {
                        offlineData.value = offlineData.value.filter((sync: any) => sync.id != id)
                        if (isEmpty(offlineData.value)) close()
                    }
                })
        }
        startSync()
        return {
            close,
            startSync,
            removeSync,
            gotoDashboard,
            toDate,
            document,
            checkmarkCircle,
            trash,
            synchronize,
            offlineData,
            synchronizationStarted
        }
    }
})
