import { AppInterface } from "../interfaces/AppInterface";
import HomeStats from "@/apps/HTS/components/HtsHomeStat.vue";
import HtsRoutes from "./config/HtsRoutes";
import KeyboardInput from "@/utils/PopupKeyboard"
import { FieldType } from "@/components/Forms/BaseFormElements";
import { Option } from "@/components/Forms/FieldInterface"
import { modalController } from "@ionic/vue";
import {
    REPORTS,
    PRIMARY_ACTIVITIES,
    SECONDARY_ACTIVITIES
} from "@/apps/HTS/config/HtsProgramActivities";
import { UserService } from "@/services/user_service";
import { find, isEmpty } from "lodash";
import { ConceptService } from "@/services/concept_service";
import { Observation } from "@/interfaces/observation";
import HisDate from "@/utils/Date"
import { ObservationService } from "@/services/observation_service";
import { Patientservice } from "@/services/patient_service";
import { HtsReportPreference } from "./composables/HtsReportComposable";
import { isCacheEnabled } from "../GLOBAL_APP/global_store";
import { HTS_PROVIDER_CODE } from "@/constants";
import { toastWarning } from "@/utils/Alerts";
import props, { HTS_PROP } from "./hts_prop";
