import { DateSelectionPreference } from "@/utils/PromptDateSelection"
import { selectActivities } from "@/utils/WorkflowTaskHelper";
import router from "@/router";
import { Offline } from "@/composables/offline"
import { ObservationOffline } from "@/interfaces/Sync";

function buildObservationDashboardCards(encounters: any, encounterName: string, targetObs: any) {
    const items: any = []
    const encounter = find(encounters, { type: { name : encounterName} })
    if (!encounter) return []
    const { observations } = encounter
    observations.forEach((obs: Observation) => {
        obs.concept.concept_names.forEach((item: any) => {
            if (targetObs[item.name]) {
                items.push(targetObs[item.name](obs, encounter))
            }
        })
    })
    return items
}

const HTS: AppInterface = {
    programID: 18, // In Programs table its recorded as HIV Testing and Counseling Program
    applicationName: 'ITS',
    applicationIcon: 'hts2.png',
    applicationDescription: "Integrated Testing Service",
    appRoutes: HtsRoutes,
    programReports: REPORTS,
    globalPropertySettings: [
        {
            name: 'Report Preferences',
            action: () => DateSelectionPreference(),
            files: []
        },
        {
            name: "Workflow Preferences",
            condition: () => UserService.isAdmin(),
            files: [
                {
                    name: "Offer recency test services",
                    pathUrl: `/preferences?label=Offer recency test services&property=${HTS_PROP.OFFER_RECENCY_TEST}`
                }
            ]
        }
    ],
    onAppSelected: async(appObj: any) => {
        const permittedRoles = [
            UserService.isProvider(),
            UserService.isAdmin()
        ]
        if (!permittedRoles.some(Boolean)) {
            return toastWarning("Your role is not permitted to access this module")
        }
        const dismissAppSelection = () => {
            modalController.dismiss(appObj, undefined, '_APPLICATION_SELECTION_MODAL_')
            router.push('/hts/kits')
        }
        await selectActivities([
            {
                id: 'hts hiv test',
                name: "HIV Test",
                icon: ''
            },
            {
                id: 'hts syphilis',
                name: "Syphilis Test",
                icon: ''
            },
            {
                id: 'hts hepatitis b',
                name: "Hepatitis B Test",
                icon: ''
            },
            {
                id: 'hts recency test',
                name: 'Recency',
                icon: 'usage.png',
                patientUrl: '/hts/recency'
            },
        ], 'HTS_PROGRAMS', 18)
        if (UserService.isAdmin()) {
            return dismissAppSelection()
        }
        if (UserService.isProvider()) {
            KeyboardInput({
                id: 'provider_code',
                helpText: 'Enter HTS Provider Code',
                type: FieldType.TT_TEXT,
                isRequired: () => true
            },
            (v: Option) => {
                UserService.getUserProperty(HTS_PROVIDER_CODE)
                    .then(code => {
                        if (code['property_value'] === `${v.value}`) {
                            dismissAppSelection()
                        } else {
                            toastWarning('Invalid provider code provided')
                        }
                    }).catch(e => toastWarning(`${e}`))
            })
        }
    },
    appStore: {
        'OFFER_RECENCY': {
            get: () => props.offerRecencyTest(),
            canReloadCache: data => !isCacheEnabled() || typeof data.state !== 'boolean'
        },
        'REPORT_HISTORY': {
            get: () => ({}),
            canReloadCache: ({state}) => isCacheEnabled() &&
                (localStorage.getItem(HtsReportPreference.CAN_CACHE) ? true : false) &&
                isEmpty(state)
        }
    },
    patientProgramInfoData(patientId) {
        let data: any = {}
        return [
            {
                label: 'HIV Status',
                value: '...',
                init: async () => {
                    try {
                        const res = await ProgramService.getProgramInformation(patientId)
                        if (res) {
                            data = res
                            return
                        }
                        const { isOnline, findByID } = Offline()
                        if (!isOnline.value) {
                            const offline = findByID(patientId)
                            const obs = (offline?.observationParams||[])
                            if (isEmpty(obs)) 
                                return
                            data.is_pregnant = obs.find((obs: ObservationOffline) => {
                                return obs.encounterName === 'pregnancy status'
                            })?.meta?.hts_female_pregnant?.label || 'N/A'

                            data.hiv_status = obs.find((obs: ObservationOffline) => {
                                return obs.encounterName === 'testing'
                            })?.meta?.hiv_result?.label || 'N/A'

                            data.is_circumcised = obs.find((obs: ObservationOffline) => {
                                return obs.encounterName === 'circumcision status'
                            })?.meta?.hts_male_circumcised || 'N/A'
                        }
                    } catch (e) {
                        console.error(e)
                    }
                },
                staticValue: () => {
                    return data.hiv_status || 'None'
                }
            },
            {
                label: 'Is Pregnant',
                value: '...',
                staticValue: () => {
                    return data.is_pregnant || 'N/A'
                },
            },
            {
                label: 'Is circumcised',
                value: '...',
                staticValue: () => {
                    return data.is_circumcised || 'N/A'
                }
            },
            {
                label: 'ART Outcome',
                value: '...',
                staticValue: () => {
                    return data.art_outcome || 'N/A'
                }
            }
        ]
    },
    confirmationSummary: (patient: Patientservice) => {
        const pID = patient?.getID()
        return {
            'Patient Identifiers': () => {
                return [
                    {
                        label: 'NPID',
                        value: '...',
                        staticValue: () => patient.getNationalID()
                    }
                ]
            },
            'GIVEN RESULTS' : () => {
                const obs = async (concept: string) =>  (await ObservationService.getFirstValueCoded(pID, concept)) || 'Unknown'
                return [
                    {
                        label: 'HIV status',
                        value: '...',
                        asyncValue: async () => obs('HIV status')
                    },
                    {
                        label: 'Hepatitis B',
                        value: '...',
                        asyncValue: async () => obs('Hepatitis B Test Result')
                    },
                    {
                        label: 'Syphilis',
                        value: '...',
                        asyncValue: async () => obs('Syphilis Test Result')
                    }
                ]
            },
            'REFERRALS' : async () => {
                const orders: Option[] = []
                await ObservationService.getAll(pID, 'Referrals ordered').then(obs => {
                    if (obs) {
                        obs.forEach((data: Observation) => {
                            orders.push({
                                label: data.value_text || '',
                                value: HisDate.toStandardHisDisplayFormat(obs.obs_datetime)
                            })
                        })
                    }
                })
                return orders
            },
            'APPOINTMENTS': () => {
                return [
                    {
                        label: 'Next appointment',
                        value: '...',
                        asyncValue: async () => {
                            const obs = await ObservationService.getFirstObs(pID, 'Appointment date')
                            if (obs) {
                                return HisDate.toStandardHisDisplayFormat(obs.value_datetime)
                            }
                            return 'N/A'
                        } 
                    },
                    {
                        label: 'ART appointment',
                        value: '...',
                        asyncValue: async () => {
                            const obs = await ObservationService.getFirstObs(pID, 'ART visit')
                            if (obs) {
                                return HisDate.toStandardHisDisplayFormat(obs.value_datetime)
                            }
                            return 'N/A'
                        } 
                    }
                ]
            },
            'ITEMS RECEIVED': () => {
                const obs = async (concept: string) => {
                    const data = await ObservationService.getFirstObs(pID, concept)
                    if (data) {
                        return `${data.value_numeric} (${HisDate.toStandardHisDisplayFormat(data.obs_datetime)})`
                    }
                    return 'N/A'
                }
                return [
                    {
                        label: 'Male condoms',
                        value: '...',
                        asyncValue: () => obs('Male condoms')
                    },
                    {
                        label: 'Female condoms',
                        value: '...',
                        asyncValue: () => obs('Female condoms')
                    },
                    {
                        label: 'FRS',
                        value: '...',
                        asyncValue: () => obs('FRS')
                    }
                ]
            },
            'OTHER': () => {
                const other: any = [
                    {
                        label: 'Marital status',
                        value: '...',
                        asyncValue: async () => {
                            return (await ObservationService.getFirstValueCoded(pID, 'Marital status')) || 'Unknown'
                        }
                    }
                ]
                if (patient.isMale()) {
                    other.push({
                        label: 'Is Circumcised',
                        value: '...',
                        asyncValue: async () => {
                            return (await ObservationService.getFirstValueCoded(pID, 'Circumcision status')) || 'Unknown'
                        }
                    })
                } else {
                    other.push({
                        label: 'Pregnancy status',
                        value: '...',
                        asyncValue: async () => {
                            return (await ObservationService.getFirstValueCoded(pID, 'Pregnancy status')) || 'Unknown'
                        } 
                    })
                }
                return other
            }
        }
    },
    overridePatientDashboardCards: () => {
        return [
            {
                id: 'c2',
                items: [],
                cache: {},
                color: 'success',
                isLoading: false,
                label: 'Appointments',
                icon: '',
                isEnabled: true,
                subscription: {
                    c1 : ({encounters, card, date}: any) => {
                        card.items = []
                        const d = (date: string) => HisDate.toStandardHisDisplayFormat(date)
                        card.items = buildObservationDashboardCards(encounters, 'APPOINTMENT', {
                            'Appointment date': (obs: any) => {
                                return {
                                    label: 'Next test',
                                    value: d(`${obs.value_datetime}`)
                                }
                            }
                        })
                        card.items = [...card.items, ...buildObservationDashboardCards(encounters, 'REFERRAL', {
                            'ART visit': (obs: any) => {
                                return {
                                    label: 'ART Date',
                                    value: d(`${obs.value_datetime}`)
                                }
                            }
                        })]
                        card.cache[date] = card.items
                    }
                },
                onClick: () => { /** Do click action for card */}
            },
            {
                id: 'c3',
                items: [],
                cache: {},
                color: 'warning',
                isLoading: false,
                label: 'Referrals',
                icon: '',
                isEnabled: true,
                subscription: {
                    c1: ({encounters, card, date}: any) => {
                        card.items = buildObservationDashboardCards(encounters, 'REFERRAL', {
                            'Referrals ordered': (obs: any, encounter: any) => {
                                return {
                                    label: obs.value_text,
                                    value: HisDate.toStandardHisTimeFormat(encounter.encounter_datetime)
                                }
                            }
                        })
                        card.cache[date] = card.items
                    }
                },
                onClick: () => { /** Do click action for card */}
            },
            {
                id: 'c4',
                items: [],
                cache: {},
                color: 'primary',
                isLoading: false,
                label: 'Given Results',
                icon: '',
                isEnabled: true,
                subscription: {
                    c1: ({ encounters, card, date }: any) => {
                        card.items = buildObservationDashboardCards(encounters, 'Testing', {
                            'HIV status': (obs: any) => {
                                return {
                                    label: 'HIV Result',
                                    value: ConceptService.getCachedConceptName(obs.value_coded || 0)
                                }
                            },
                            'Hepatitis B Test Result': (obs: any) =>  {
                                return {
                                    label: 'Hepatitis B Result',
                                    value: ConceptService.getCachedConceptName(obs.value_coded || 0)
                                }
                            },
                            'Syphilis Test Result': (obs: any) =>  {
                                return {
                                    label: 'Syphilis Result',
                                    value: ConceptService.getCachedConceptName(obs.value_coded || 0)
                                }
                            }
                        })
                        card.cache[date] = card.items
                    }
                },
                onClick: () => { /** Do click action for card */}
            }
        ]
    },
    homeOverviewComponent: HomeStats,
    primaryPatientActivites: PRIMARY_ACTIVITIES,
    secondaryPatientActivites: SECONDARY_ACTIVITIES
}
export default HTS
